@mixin ab($t, $l, $b, $r) {
    position: absolute;    
    @if $l != n { left: $l; }
    @if $t != n { top: $t; }
    @if $r != n { right: $r; }
    @if $b != n { bottom: $b; }
}
@mixin rotate($rotate) { 
  transform: rotate($rotate);
}
@mixin padding-start($value) {
    padding-inline-start: $value;
}
@mixin padding-end($value) {
    padding-inline-end: $value;
}
@mixin margin-start($value) {
    margin-inline-start: $value;
}
@mixin margin-end($value) {
    margin-inline-end: $value;
}
@mixin text-right {
    text-align: end;
}
@mixin text-left {
    text-align: start;
}


.section-icon-list {
	.section-container {
		position: relative; 
	} 
	.flex-container {
		margin: 0 auto;
	}
	&.show-arrows {		
		.flex >.block {
			position: relative;
			&:not(:last-child) {
				&:after {
					content: url("data:image/svg+xml,%3Csvg width='20' height='10' viewBox='0 0 20 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9.84362 5.92885L2.57202 2.47086e-08L0.5 2.68775L10 10L19.5 2.68775L17.428 2.01864e-07L9.84362 5.92885Z' fill='%23FE8F17'/%3E%3C/svg%3E%0A");
					margin: 20px 0;
					display: block;
					text-align: center;
				}
			}
		}
	}
	.block-inner {
		max-width: 300px;
		margin: auto;
		flex-wrap: nowrap;
	}
	.icon-type-svg {		
		display: flex;
	}
	.icon {
		flex: none;
		@include margin-end(20px);
		> * {
			pointer-events: none;
		}
		svg {
			width: inherit;
			height: inherit;
		}
	}
	@media (max-width: 749px) {
		&.style-vertical {
			.icon {
				min-height: 44px;
			}
		}
		.group-text {
			@include text-left;
		}
		.block-inner {
			margin-bottom: 25px;
		}
	}
	.text-1 {
		font-size: 16px;
		line-height: 1.3em;
	}
	.text-2 {
		line-height: 1.47em;
		color: var(--subtext-color);
		margin-top: 5px;
	}
	&.show-bottom-border {
		.section-content-inner {
			border-bottom: 1px solid var(--miscellaneous-color);
		}
	}
	@media (min-width: 750px) {
		&.show-arrows {			
			.flex >.block {
				&:not(:last-child) {
					&:after {
						margin: -10px 0 0;
						@include ab(50%,n,n,-10px);
						@include rotate(-90deg);
					}
				}
			}
		}
		.block-inner {
			flex-wrap: wrap;
		}
		&.style-vertical {
			.icon {
				margin: 0 auto 20px;
			}
			.block-inner {
				text-align: center;
				height: 100%;
				display: flex;
			}
			.group-text {
				width: 100%;
				margin-top: auto;
				display: flex;
				flex-wrap: wrap;
			}
			.text-1 {
				line-height: 1.3em;
				width: 100%;
			}
			.text-2 {
				margin-top: 10px;
				width: 100%;
			}
		}
		&.style-horizontal {
			.icon {
			 @include margin-end(0);
			}
			.text-1 {
				font-size: 18px;
			}
			.group-text {
				width: 100%;
				text-align: center;
				margin-top: 10px;
			}
			.block-inner {
				justify-content: center;
			}
			&:not(.show-arrows) {
				.icon {
					@include margin-end(12px);
				}
			}
		}
	}
	@media (min-width: 1000px) {
		&.style-vertical {
			.text-1 {
				font-size: 22px;
			}
			.text-2 {
				margin-top: 20px;
			}
		}
		&.style-horizontal {
			.icon {
				@include margin-end(30px);
			}
			.group-text {
				width: auto;
				@include text-left;
				margin-top: 0;
			}
			.block-inner {
				justify-content: center;
			}
		}
	}
} 
.section-icon-list-slide {
	.section-container {
		position: relative;
	}
	.tns-nav {
		position: relative;
		bottom: 0;
		padding-top: 20px;
	}
	.icon {
		svg {
			width: inherit;
			height: inherit;
		}
	}
	.text {
		font-size: 16px;
	}
	&.show-bottom-border {
		.section-content-inner {
			border-bottom: 1px solid var(--miscellaneous-color);
		}
	}
	&.style-vertical {
		.text {
			margin-top: 12px;
		}
		.block-inner {
			height: 100%;
		}
		.icon-type-svg {
			display: flex;
		}
		.icon {
			min-height: 44px;
		}
	}
	&.style-horizontal {
		.text {
			@include margin-start(10px);
		}
		.block-inner {
			flex-wrap: nowrap;
			justify-content: center;
		}
	}
	@media (min-width: 1000px) {
		.tns-nav {
			display: none;
		}
	}
}